/*-------------------------------------------------
>> Normalize styles
-------------------------------------------------*/
@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@500;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@900&display=swap");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 10px;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

a {
  background-color: transparent;
  -webkit-font-smoothing: antialiased;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
}

img {
  border-style: none;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

/*-------------------------------------------------
>> Default styles for forms
-------------------------------------------------*/
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: inherit;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
 * Remove the inner border and padding in Firefox.
 */
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus styles unset by the previous rule.
 */
button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.6rem 1.2rem 1rem;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

/**
 * Add the correct box sizing in IE 10.
 */
[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

/**
 * Remove the inner padding in Chrome and Safari on macOS.
 */
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

/*-------------------------------------------------
>> Clearing styles
-------------------------------------------------*/
.clear {
  content: "";
  display: table;
  table-layout: fixed;
  clear: both;
}

.clear:after,
.clear:before {
  clear: both;
}

/*-------------------------------------------------
>> Default styles for gravity forms
-------------------------------------------------*/
.gform_wrapper ul {
  margin: 0;
}
.gform_wrapper ul li {
  list-style-type: none;
  background: none;
}
.gform_wrapper ul li:before {
  display: none;
}

.gfields {
  margin: 0;
}

.gfield {
  box-sizing: border-box;
  margin-bottom: 0;
  position: relative;
}

.page-content ul li.gfield.half-input-field,
.gfields ul li.gfield.half-input-field {
  width: 48%;
  margin-right: 1.5%;
  display: inline-block;
}
.page-content ul li.gfield,
.gfields ul li.gfield {
  list-style-image: none;
  padding-left: 0;
  float: none;
  width: 100%;
  list-style: none;
}

.gfield label {
  display: block;
  margin-bottom: 10px;
  font-size: 16px;
}

.gfield_checkbox,
.gfield_radio {
  padding-bottom: 2rem !important;
  margin-bottom: 3rem !important;
}
.gfield_checkbox li,
.gfield_radio li {
  position: relative;
  padding-left: 40px !important;
  margin-bottom: 20px !important;
}
.gfield_checkbox li label:after,
.gfield_radio li label:after {
  content: "";
  width: 20px;
  height: 20px;
  display: block;
  position: absolute;
  left: 0;
  top: 2px;
  border-radius: 3px;
  z-index: 0;
  border: 1px solid rgba(109, 122, 135, 0.3);
}
.gfield_checkbox li label:before,
.gfield_radio li label:before {
  content: "";
  display: block;
  position: absolute;
  left: 2px !important;
  top: 4px;
  width: 16px;
  height: 16px;
  background-color: #000000;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -o-border-radius: 4px;
  opacity: 0;
  z-index: 1;
  transition: all 100ms;
}

#ui-datepicker-div {
  background-color: #fff !important;
}

.ui-datepicker-title select {
  width: 49%;
  margin-bottom: 0;
  background-color: #fff;
  padding-right: 3rem;
  margin-right: 2%;
  height: 56px;
  padding-top: 16px;
  padding-bottom: 16px;
}

a.ui-datepicker-next.ui-corner-all {
  float: right;
}

.ui-datepicker-header a,
.ui-datepicker-header a:visited {
  font-size: 16px;
  color: #ffffff;
  font-weight: 500;
  cursor: pointer;
  margin-bottom: 1rem;
  display: inline-block;
}

select.ui-datepicker-year {
  margin-right: 0;
}

#ui-datepicker-div table {
  border-collapse: collapse;
  border-spacing: 0;
  margin: 0 0 1rem 0;
  font-size: 16px;
}

.ui-datepicker-calendar a {
  font-size: 16px;
}

img.ui-datepicker-trigger {
  position: absolute;
  right: 10px;
  top: 0;
  bottom: 0;
  margin: auto;
}

.gfield_radio li {
  display: inline-block;
  margin-right: 40px;
  padding-left: 0 !important;
}
.gfield_radio li label {
  display: inline-block;
  padding-left: 10px;
}

.ginput_complex {
  padding-bottom: 2rem !important;
  margin-bottom: 2rem !important;
}
.ginput_complex input {
  margin-bottom: 0;
}

#ui-datepicker-div table th:nth-child(1) {
  text-align: center;
}

#ui-datepicker-div table th {
  font-weight: 600;
  padding: 0.5rem 0.8rem;
}

#ui-datepicker-div table td:nth-child(1) {
  text-align: center;
}

#ui-datepicker-div table td {
  padding: 0.5rem 1.8rem;
  text-align: center;
}

#ui-datepicker-div table tr:nth-child(even) {
