}

.validation_message {
  font-size: 14px;
  color: #fff;
  text-align: center;
  padding: 2px 10px 4px 10px;
  display: inline-block;
  position: absolute;
  bottom: 12px;
}

.gform_confirmation_message {
  background-color: #fff;
  padding: 20px;
  text-align: center;
  color: green;
}

/*------------------------------------------------------------------
> Typography
------------------------------------------------------------------*/
/*-------------------------------------------------
>> Styles for headings
-------------------------------------------------*/
h1,
.heading-1,
h2,
.heading-2,
h3,
.heading-3,
h4,
.heading-4,
h5,
.heading-5 h6,
.heading-6 {
  margin: 0 0 2.5rem 0;
  text-transform: initial;
  clear: both;
  line-height: 1.1;
}

h1,
.heading-1 {
  font-size: 56px;
  font-size: 5.6rem;
  line-height: 1;
  margin-bottom: 2.5rem;
}

h2,
.heading-3 {
  font-size: 52px;
  font-size: 5.2rem;
}

h3,
.heading-3 {
  font-size: 28px;
  font-size: 2.8rem;
}

h4,
.heading-4 {
  font-size: 22px;
  font-size: 2.2rem;
}

h5,
.heading-5 {
  font-size: 20px;
  font-size: 2rem;
}

h6,
.heading-6 {
  font-size: 18px;
  font-size: 1.8rem;
}

/*-------------------------------------------------
>> Styles for content tags
-------------------------------------------------*/
i,
cite,
em,
var,
address,
dfn {
  font-style: italic;
}

code,
pre,
kbd,
tt,
var,
samp {
  font-family: monospace, monospace;
  font-size: 16px;
  font-size: 1.6rem;
}

abbr,
acronym {
  border-bottom: 1px dotted;
  cursor: help;
}

abbr[title] {
  text-decoration: none;
}

address {
  margin: 0 0 4rem 0;
}

b,
strong {
  font-weight: 500;
  color: #000000;
}

small {
  font-size: 80%;
}

big {
  font-size: 125%;
}

s,
strike,
del {
  text-decoration: line-through;
}

del {
  color: #ff6b6b;
}

u,
ins {
  text-decoration: underline;
}

ins {
  color: #1dd1a1;
}

sub,
sup {
  font-size: 70%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

code {
  background-color: #080807;
  padding: 3rem 3rem 3rem 3.5rem;
  display: block;
  margin: 0 0 4rem 0;
  line-height: 1.5;
  color: #ffffff;
  max-width: 100%;
  overflow: auto;
}

pre {
  background-color: #080807;
  margin: 0 0 4rem 0;
  padding: 3rem 3rem 3rem 3.5rem;
  overflow-x: scroll;
  line-height: 1.5;
  color: #ffffff;
  max-width: 100%;
  overflow: auto;
}

embed,
iframe,
object {
  max-width: 100%;
}

hr {
  border: 0;
  height: 1px;
  margin-bottom: 4rem;
}

/*------------------------------------------------------------------
> Elements
------------------------------------------------------------------*/
/*-------------------------------------------------
>> Styles for lists
-------------------------------------------------*/
ul,
ol {
  margin: 0 0 4.5rem 2.5rem;
}

ul {
  list-style: none;
  margin: 0 0 5rem 0;
}

.glide-block ul,
.page-content ul,
.banner-container ul {
  list-style: none;
}
.glide-block ul li,
.page-content ul li,
.banner-container ul li {
  padding-left: 50px;
  position: relative;
}
.glide-block ul li:before,
.page-content ul li:before,
.banner-container ul li:before {
  content: "";
  width: 6px;
  height: 6px;
  background-position: left top;
  position: absolute;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -o-border-radius: 50%;
  left: 0;
  top: 10px;
}

.glide-block ol li,
.page-content ol li,
.banner-container ol li {
  position: relative;
  counter-increment: list;
  padding-left: 50px;
}
.glide-block ol li:before,
.page-content ol li:before,
.banner-container ol li:before {
  color: #000000;
  content: counter(list) ".";
  left: 0;
  top: 0px;
  position: absolute;
  width: 26px;
}

ol {
  list-style: none;
  margin: 0 0 5rem 0;
}

ul li,
ol li {
  margin: 0 0 1rem 0;
}

ul ul,
ol ol {
  margin: 2rem 0 0 5rem;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 2rem;
}

dt {
  font-weight: bold;
}

dl,
dd {
  margin: 0 0 4rem 0;
}

/*-------------------------------------------------
>> Styles for quotes
-------------------------------------------------*/
blockquote,
q {
  quotes: none;
}

blockquote {
  position: relative;
  box-shadow: none;
  padding: 5rem 5rem 3.6rem 5rem;
  margin-bottom: 2.4rem;
}
blockquote cite {
  display: block;
  margin-top: 2rem;
  font-size: 16px;
  font-size: 1.6rem;
  text-transform: uppercase;
  color: rgba(22, 41, 72, 0.45);
  font-weight: 500;
  font-family: "IBM Plex Mono", monospace;
  font-style: normal;
  position: relative;
  padding-left: 2rem;
}
blockquote cite:before {
  content: "-";
  font-size: 30px;
  font-size: 3rem;
  height: 10px;
  display: inline-block;
  position: absolute;
  left: 0px;
  top: -11px;
}
blockquote p {
  margin-bottom: 2.2rem;
  font-size: 22px;
  font-size: 2.2rem;
}
blockquote p:before {
  content: '"';
  position: absolute;
  left: 35px;
}
blockquote p:after {
  content: '"';
}

q {
  quotes: "“" "”";
  background-color: #ffffff;
  border-radius: 3px;
  padding: 0.4rem 0.5rem 0.5rem 3rem;
  position: relative;
}
q:before {
  font-size: 30px;
  font-size: 3rem;
  position: absolute;
  content: open-quote;
  left: 7px;
  top: -8px;
}
q:after {
  display: none;
}

/*-------------------------------------------------
>> Styles for tables
-------------------------------------------------*/
table {
  border-collapse: collapse;
  border-spacing: 0;
  margin: 0 0 4rem 0;
  width: 100%;
  table-layout: fixed;
}

th {
  font-weight: 700;
  padding: 1.5rem 1.8rem;
}

tr {
  vertical-align: middle;
}
tr:nth-child(even) {
  background-color: #eaeaea;
}

td {
  padding: 1.3rem 1.8rem;
  vertical-align: middle;
}
td:nth-child(1) {
  text-align: left;
}

/*-------------------------------------------------
>> Styles for images and figures
-------------------------------------------------*/
.featured-image {
  width: 100%;
  margin: 7rem 0 5rem 0;
  text-align: center;
}

img {
  height: auto;
  max-width: 100%;
}

a img {
  border: none;
  outline: none;
  margin-bottom: 0;
}

figure {
  margin: 1.6rem 0;
}

figcaption {
  font-size: 18px;
  font-size: 1.8rem;
  margin: 0;
}

.wp-block-gallery li {
  padding: 0 !important;
}
.wp-block-gallery li:before {
  display: none;
}

/*-------------------------------------------------
>> Styles for links
-------------------------------------------------*/
a {
  text-decoration: none;
  transition: all 0.4s ease-in-out;
  outline: none;
}

/*------------------------------------------------------------------
> Forms
------
------------------------------------------------------------*/
/*-------------------------------------------------
>> Styles for form fields
-------------------------------------------------*/
input[type=text],
input[type=password],
input[type=search],
.input-comments,
.search-input,
input[type=email],
input[type=url],
input[type=tel],
input[type=number],
input[type=date],
input[type=file],
input[type=range] {
  height: 60px;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(109, 122, 135, 0.3);
  outline: none;
  background-color: #ffffff;
  font-size: 18px;
  font-size: 1.8rem;
  padding: 0 0 0 1.8rem;
  margin: 0 0 4rem 0;
  max-width: 100%;
  -webkit-transition: all 0.4s ease-in-out;
  -moz-transition: all 0.4s ease-in-out;
  -ms-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -o-border-radius: 6px;
}
input[type=text]:focus,
input[type=password]:focus,
input[type=search]:focus,
.input-comments:focus,
.search-input:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=tel]:focus,
input[type=number]:focus,
input[type=date]:focus,
input[type=file]:focus,
input[type=range]:focus {
  border: 1px solid;
}

input[type=file] {
  padding: 0.9rem;
}

select {
  height: 60px;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(109, 122, 135, 0.3);
  outline: none;
  background-color: #ffffff;
  font-size: 18px;
  font-size: 1.8rem;
  padding: 0 0 0 1.8rem;
  margin: 0 0 4rem 0;
  max-width: 100%;
  -webkit-transition: all 0.4s ease-in-out;
  -moz-transition: all 0.4s ease-in-out;
  -ms-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -o-border-radius: 6px;
  cursor: pointer;
  -webkit-appearance: none;
  background-image: url(../img/select-arrows.svg);
  background-repeat: no-repeat;
  background-position: center right 17px;
}
select:focus {
  border: 1px solid;
}

option {
  font-weight: 300 !important;
}

input[type=range] {
  padding: 0;
  border: none;
}

textarea {
  width: 100%;
  height: 150px;
  border: 1px solid rgba(109, 122, 135, 0.3);
  padding: 1rem 0 0 1.8rem;
  margin: 0 0 4rem 0;
  box-sizing: border-box;
  background-color: #ffffff;
  font-size: 18px;
  font-size: 1.8rem;
  outline: none;
  -webkit-transition: all 0.4s ease-in-out;
  -moz-transition: all 0.4s ease-in-out;
  -ms-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  height: 160px;
  resize: none;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -o-border-radius: 6px;
}
textarea:focus {
  border: 1px solid;
}

input[type=checkbox] {
  opacity: 0;
  outline: none;
  z-index: 100;
  width: 27px;
  height: 27px;
  top: 0;
  left: 0;
  position: absolute;
  appearance: none;
  cursor: pointer;
}
input[type=checkbox] + label {
  cursor: pointer;
}
input[type=checkbox]:checked + label:before {
  opacity: 1;
}
input[type=checkbox]:checked + label:after {
  border: 1px solid;
}

/*-------------------------------------------------
>> Styles for buttons
-------------------------------------------------*/
input[type=submit],
button,
input[type=button] {
  padding: 20px 25px;
  display: inline-block;
  line-height: 1;
  box-sizing: border-box;
  border: 1px solid transparent;
  font-size: 18px;
  color: #ffffff;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -o-border-radius: 10px;
  height: 60px;
  outline: none;
  cursor: pointer;
  margin: 0 auto;
}

/*------------------------------------------------------------------
> Media
------------------------------------------------------------------*/
/*-------------------------------------------------
>> Styles for WordPress captions
-------------------------------------------------*/
.wp-caption {
  max-width: 100%;
  box-sizing: border-box;
  margin-bottom: 5rem !important;
}
.wp-caption.alignnone {
  margin: 1.5rem 0 1.5rem 0;
}
.wp-caption.alignleft {
