    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }

  .nav-overlay.open {
    opacity: 1;
    visibility: visible;
    overflow: visible;
  }

  header .nav-container {
    padding: 95px 3% 1rem 3%;
    opacity: 0;
    height: 100%;
    -webkit-transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;
    -ms-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
    transition-delay: 0.25s;
    box-sizing: border-box;
  }

  header .nav-overlay.open .nav-container {
    opacity: 1;
    padding: 82px 3% 1rem 3%;
  }

  header.shrink .nav-container {
    padding: 84px 3% 1rem 3%;
  }

  header.shrink .nav-overlay.open .nav-container {
    opacity: 1;
    padding: 71px 3% 1rem 3%;
  }

  .nav-content {
    height: 100%;
    max-height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
    padding-right: 2%;
    padding-left: 2%;
    box-sizing: border-box;
  }

  .nav-content::-webkit-scrollbar-track {
    box-shadow: none;
    background-color: transparent;
  }

  .nav-content::-webkit-scrollbar {
    width: 3px;
    background-color: transparent;
    position: relative;
    left: 5px;
    box-shadow: none;
  }

  .nav-content::-webkit-scrollbar-thumb {
    box-shadow: none;
    border-radius: 2px;
  }

  .header-btns {
    float: none;
    width: 100%;
    text-align: center;
    display: block;
    opacity: 0;
    -webkit-transition: all 0.55s ease-in-out;
    -moz-transition: all 0.55s ease-in-out;
    -ms-transition: all 0.55s ease-in-out;
    -o-transition: all 0.55s ease-in-out;
    transition: all 0.55s ease-in-out;
    transition-delay: 0.4s;
    padding-top: 10px;
  }

  .nav-overlay.open .header-btns {
    opacity: 1;
    padding-top: 0px;
  }

  .menu-btn {
    display: block;
    position: fixed;
    top: 22px;
    right: 5%;
    height: 27px;
    width: 35px;
    cursor: pointer;
    z-index: 9999;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }

  header.shrink .menu-btn {
    top: 16px;
  }

  .menu-btn.active .top {
    -webkit-transform: translateY(11px) translateX(0) rotate(-45deg);
    transform: translateY(11px) translateX(0) rotate(-45deg);
    background: #ffffff;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }

  .menu-btn.active .middle {
    opacity: 0;
    background: #ffffff;
  }

  .menu-btn.active .bottom {
    -webkit-transform: translateY(-11px) translateX(0) rotate(45deg);
    transform: translateY(-11px) translateX(0) rotate(45deg);
    background: #ffffff;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }

  .menu-btn span {
    background: #ffffff;
    border: none;
    height: 5px;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    cursor: pointer;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }

  .menu-btn.active span {
    left: 3px;
  }

  .menu-btn span:nth-of-type(2) {
    top: 11px;
  }

  .menu-btn span:nth-of-type(3) {
    top: 22px;
  }

  li.menu-item-has-children .submenu-icon {
    position: absolute;
    z-index: 9999;
    right: -8px;
    top: 5px;
    display: block;
    height: 30px;
    width: 30px;
    cursor: pointer;
  }
  li.menu-item-has-children .submenu-icon:after {
    position: absolute;
    top: 14px;
    right: 8px;
    width: 11px;
    height: 1px;
    display: block;
    background: #ffffff;
    content: "";
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  li.menu-item-has-children .submenu-icon:before {
    position: absolute;
    top: 9px;
    right: 13px;
    width: 1px;
    height: 12px;
    display: block;
    background: #ffffff;
    content: "";
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }

  li.menu-item-has-children.active > .submenu-icon:before {
    height: 0;
    top: 18px;
  }
}
